/* ==================== GLOBAL STYLES ==================== */
body {
  margin: 0;
  font-family: "Inter", sans-serif;
  line-height: 1.6;
  background: #ffffff;
  color: #111827;
  box-sizing: border-box;
  padding-top: 70px;
}

:root {
  --primary-red: #dc2626;
  --primary-green: #16a34a;
  --primary-blue: #2563eb;
  --accent-orange: #ea580c;
}

.App {
  padding: 20px 3vw;
  max-width: 960px;
  margin: 0 auto;
  box-sizing: border-box;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 3vw;
  padding-right: 3vw;
  box-sizing: border-box;
}

/* ==================== SECTION PADDING ==================== */
header,
.hero-section,
.analytics-section,
.stories-section,
.scam-types-section {
  padding: 10px 10px 35px 10px;
}

/* ==================== SECTION HEADER ==================== */
.section-header {
  display: block;
    text-align: center;
    margin-bottom: 40px;
}
.section-title {
    font-size: 2.5rem;
    font-weight: 700;
    color: #000000;
    margin-bottom: 5px;
    margin-top: 0;
}
.section-subtitle {
    font-size: 1rem;
    color: #64748b;
    max-width: 700px;
    margin: 0 auto;
}
/* ==================== FLOATING BUTTON ==================== */
.report-btn-float {
  display: none;
  position: fixed;
  right: 20px;
  bottom: 20px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: #2563eb;
  color: #fff;
  font-size: 1.5rem;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.4);
  border: none;
  z-index: 1020;
  cursor: pointer;
  transition: all 0.3s;
}

.report-btn-float:hover {
  background: #1e40af;
  transform: scale(1.1);
}

.report-btn-float:active {
  background: #1e3a8a;
  transform: scale(1.05);
}

/* ==================== MODAL OVERLAY ==================== */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  overflow-y: auto;
}

/* ==================== REPORT MODAL ==================== */
.report-modal {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 550px;
  width: 100%;
  padding: 32px 28px;
  position: relative;
  max-height: 90vh;
  overflow-y: auto;
  margin: auto;
  animation: modalSlideIn 0.3s ease;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* ==================== MODAL HEADER ==================== */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid #e5e7eb;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111827;
  margin: 0;
}

.close-btn {
  background: transparent;
  border: none;
  font-size: 2rem;
  color: #9ca3af;
  cursor: pointer;
  transition: all 0.2s;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
}

.close-btn:hover {
  color: #ef4444;
  background: #fee2e2;
}

.close-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* ==================== REPORT FORM ==================== */
.report-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* ==================== FORM ROW ==================== */
.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

/* ==================== FORM GROUP ==================== */
.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-label {
  font-weight: 600;
  color: #374151;
  font-size: 0.95rem;
  margin: 0;
}

/* ==================== FORM INPUTS ==================== */
.form-input,
.form-textarea,
select {
  font-size: 1rem;
  border-radius: 8px;
  border: 1.5px solid #d1d5db;
  padding: 12px;
  outline: none;
  background: #f9fafb;
  color: #111827;
  transition: all 0.2s;
  font-family: inherit;
  width: 100%;
  box-sizing: border-box;
}

.form-input:focus,
.form-textarea:focus,
select:focus {
  border-color: #2563eb;
  background: #fff;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.form-input:disabled,
.form-textarea:disabled,
select:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: #f3f4f6;
}

.form-textarea {
  min-height: 80px;
  resize: vertical;
  line-height: 1.5;
}

/* ==================== BUTTON ROW ==================== */
.button-row {
  display: flex;
  gap: 12px;
  margin-top: 8px;
}

.button-row .btn {
  flex: 1;
}

/* ==================== BUTTONS ==================== */
.btn {
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  padding: 12px 24px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-family: inherit;
}

.btn-primary {
  background: #2563eb;
  color: #fff;
}

.btn-primary:hover:not(:disabled) {
  background: #1e40af;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.3);
}

.btn-primary:active:not(:disabled) {
  background: #1e3a8a;
  transform: translateY(0);
}

.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-secondary {
  background: #f3f4f6;
  color: #374151;
  border: 1.5px solid #d1d5db;
}

.btn-secondary:hover:not(:disabled) {
  background: #e5e7eb;
  border-color: #9ca3af;
}

.btn-secondary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ==================== LOADING SPINNER ==================== */
.fa-spinner {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ==================== SUCCESS POPUP ==================== */
.success-popup {
  background: #fff;
  width: 100%;
  max-width: 450px;
  border-radius: 16px;
  padding: 40px 32px;
  text-align: center;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: successFadeIn 0.3s ease;
  margin: auto;
}

@keyframes successFadeIn {
  from {
    opacity: 0;
    transform: scale(0.9) translateY(20px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.success-icon {
  font-size: 4rem;
  color: #16a34a;
  margin-bottom: 20px;
  animation: successIconBounce 0.6s ease;
}

@keyframes successIconBounce {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.success-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #111827;
  margin: 0 0 12px 0;
}

.success-message {
  font-size: 1rem;
  color: #6b7280;
  line-height: 1.6;
  margin: 0 0 28px 0;
}

.success-btn {
  min-width: 140px;
  padding: 12px 32px;
}

/* ==================== SCROLLBAR STYLING ==================== */
.report-modal::-webkit-scrollbar {
  width: 6px;
}

.report-modal::-webkit-scrollbar-track {
  background: #f3f4f6;
  border-radius: 10px;
}

.report-modal::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 10px;
}

.report-modal::-webkit-scrollbar-thumb:hover {
  background: #9ca3af;
}

/* ==================== RESPONSIVE ==================== */
@media (max-width: 1024px) {
  .section-title {
    font-size: 2rem;
  }

  .section-subtitle {
    font-size: 1rem;
  }
}

@media (max-width: 900px) {
  .App,
  .container {
    max-width: 100vw;
    padding-left: 2vw;
    padding-right: 2vw;
  }
}

@media (max-width: 780px) {
  .report-btn-float {
    display: flex;
  }
  .header-btn {
    display: none;
  }
  .hide-on-mobile {
    display: none;
  }
}

@media (max-width: 768px) {
  .modal-overlay {
    padding: 16px;
  }

  .report-modal {
    padding: 24px 20px;
    max-height: 85vh;
  }

  .modal-title {
    font-size: 1.3rem;
  }

  .form-row {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .button-row {
    flex-direction: column;
  }

  .button-row .btn {
    width: 100%;
  }

  .success-popup {
    padding: 32px 24px;
  }

  .success-icon {
    font-size: 3rem;
  }

  .success-title {
    font-size: 1.3rem;
  }

  .success-message {
    font-size: 0.95rem;
  }
}

@media (max-width: 600px) {
  .App,
  .container {
    max-width: 100vw;
    padding-left: 0;
    padding-right: 0;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 1.75rem;
  }


  .report-modal {
    padding: 20px 16px;
  }

  .modal-title {
    font-size: 1.2rem;
  }

  .btn {
    font-size: 0.95rem;
    padding: 10px 20px;
  }
}
